<template>
    <b-overlay :show="isLoading" >
        <form @submit.prevent="EditLine()">
            <div class="mb-3">
                                    
                                    
                        <div class="form-group">
                            <label>libelle </label>
                                                            <input type="text" :class="errors.libelle?'form-control is-invalid':'form-control'"
                                       v-model="form.libelle">
                            
                            <div class="invalid-feedback" v-if="errors.libelle">
                                <template v-for=" error in errors.libelle" >  {{error[0]}}</template>

                            </div>
                        </div>
                    
                                    
                        <div class="form-group">
                            <label>code </label>
                                                            <input type="text" :class="errors.code?'form-control is-invalid':'form-control'"
                                       v-model="form.code">
                            
                            <div class="invalid-feedback" v-if="errors.code">
                                <template v-for=" error in errors.code" >  {{error[0]}}</template>

                            </div>
                        </div>
                    
                                    
                        <div class="form-group">
                            <label>site </label>
                                                            <input type="text" :class="errors.site?'form-control is-invalid':'form-control'"
                                       v-model="form.site">
                            
                            <div class="invalid-feedback" v-if="errors.site">
                                <template v-for=" error in errors.site" >  {{error[0]}}</template>

                            </div>
                        </div>
                    
                                    
                        <div class="form-group">
                            <label>zone </label>
                                                            <input type="text" :class="errors.zone?'form-control is-invalid':'form-control'"
                                       v-model="form.zone">
                            
                            <div class="invalid-feedback" v-if="errors.zone">
                                <template v-for=" error in errors.zone" >  {{error[0]}}</template>

                            </div>
                        </div>
                    
                

                                </div>

            <div class="d-flex justify-content-between">
                <button type="submit" class="btn btn-primary" >
                    <i class="fas fa-floppy-disk"></i> Mettre à jour
                </button>
                <button type="button" class="btn btn-danger" @click.prevent="DeleteLine()">
                    <i class="fas fa-close"></i> Supprimer
                </button>
            </div>
        </form>
    </b-overlay>
</template>

<script>
    import $ from 'jquery'
    import Files from "@/components/Files.vue"
    import CustomSelect from "@/components/CustomSelect.vue"
    export default {
        name: 'EditPostesglobals',
        components: { CustomSelect,Files },
        props: ['data','gridApi','modalFormId',
                    ],
        data () {
            return {
                errors: [],
                isLoading: false,
                form: {
                    
                        id:"",
                    
                        libelle:"",
                    
                        code:"",
                    
                        site:"",
                    
                        zone:"",
                                    }
            }
        },

        mounted () {
            this.form = this.data
                                                                                                                                                                },
        methods: {

            EditLine () {
                this.isLoading = true
                this.axios.post('/api/postesglobals/' + this.form.id + '/update', this.form).then(response => {
                    this.isLoading = false
                    this.gridApi.applyServerSideTransaction({
                        update: [
                            response.data
                        ],
                    });
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    this.errors = error.response.data.errors
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de l\'enregistrement')
                })
            },
            DeleteLine () {
                this.isLoading = true
                this.axios.post('/api/postesglobals/' + this.form.id + '/delete').then(response => {
                    this.isLoading = false

                    this.gridApi.applyServerSideTransaction({
                        remove: [
                            this.form
                        ]
                    });
                    this.gridApi.refreshServerSide()
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    console.log(error.response.data)
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de la suppression')
                })
            },
        }
    }
</script>
